<template>
  <div>
    <h3>Paso 3: Datos de los inscritos</h3>

    <!-- Checkbox para incluir menor de 2 años -->
    <div class="form-check mb-3">
      <input
        class="form-check-input"
        type="checkbox"
        v-model="conBebes"
        id="menor2"
        @change="toggleBebe"
      />
      <label class="form-check-label" for="menor2">
        Voy con un menor de 2 años
      </label>
    </div>

    <!-- Campos dinámicos de año y botón -->
    <div v-if="conBebes" class="mb-3">
      <label for="edad" class="form-label">Edad del menor (en meses)</label>
      <select v-model="edadBebe" class="form-select">
        <option value="">Seleccione...</option>
        <option v-for="mes in 24" :key="mes" :value="mes">{{ mes }} meses</option>
      </select>
      <button class="btn btn-primary mt-2" @click="addBebe">Añadir menor</button>
    </div>

    <!-- Fichas de inscritos -->
    <div
      v-for="(inscrito, index) in inscritos"
      :key="'inscrito_' + index"
      :class="['card', 'mb-3', { 'bg-light': showBebe[index] }]"
      :style="showBebe[index] ? 'border-left: 5px solid #0d6efd;' : ''"
    >
      <div class="card-body">
        <h5 class="card-title">
          Inscrito {{ index + 1 }}
          <span v-if="showBebe[index]" class="badge bg-primary">Menor</span>
        </h5>

        <div class="mb-3">
          <label for="nombre" class="form-label">Nombre</label>
          <input type="text" v-model="inscrito.nombre" class="form-control" placeholder="Nombre del inscrito" />
        </div>

        <div class="mb-3">
          <label for="pseudonimo" class="form-label">Pseudónimo</label>
          <input type="text" v-model="inscrito.pseudonimo" class="form-control" placeholder="Pseudónimo (opcional)" />
        </div>

        <div class="row">
          <div class="col-6 mb-3">
            <label for="email" class="form-label">
              <i class="fas fa-envelope"></i> Correo electrónico
            </label>
            <input
              type="email"
              v-model="inscrito.email"
              class="form-control"
              placeholder="Correo electrónico"
            />
          </div>
          <div class="col-6 mb-3">
            <label for="telefono" class="form-label">
              <i class="fas fa-phone"></i> Teléfono
            </label>
            <input
              type="tel"
              v-model="inscrito.telefono"
              class="form-control"
              placeholder="Teléfono"
              pattern="[0-9]{9}"
            />
          </div>
        </div>

        <div v-if="showBebe[index]" class="alert alert-info">
          <i class="fas fa-baby"></i> Este inscrito es un menor (menor de 2 años)
        </div>

        <div class="form-check">
          <input
            class="form-check-input"
            type="checkbox"
            v-model="inscrito.aceptaCondiciones"
            :id="'acepta_' + index"
          />
          <label class="form-check-label" :for="'acepta_' + index">
            Acepto las condiciones
          </label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      conBebes: false, // Checkbox principal para menores de 2 años
      edadBebe: null, // Edad del bebé en meses
      inscritos: [], // Lista de inscritos
      showBebe: [], // Indica si un inscrito es un bebé
    };
  },
  methods: {
    toggleBebe() {
      if (!this.conBebes) {
        // Si se desmarca el checkbox, elimina los menores ya añadidos
        this.inscritos = this.inscritos.filter((_, index) => !this.showBebe[index]);
        this.showBebe = this.showBebe.filter((isBebe) => !isBebe);
      }
    },
    addBebe() {
      if (!this.edadBebe) {
        alert("Por favor, seleccione la edad del menor.");
        return;
      }

      // Crear nueva ficha para menor
      this.inscritos.push({
        nombre: "",
        pseudonimo: "",
        email: "",
        telefono: "",
        aceptaCondiciones: false,
      });
      this.showBebe.push(true); // Marcar este inscrito como menor
    },
  },
};
</script>

<style>
.card.bg-light {
  background-color: #f8f9fa !important; /* Fondo claro para menores */
}

.card.bg-light .badge.bg-primary {
  background-color: #0d6efd !important; /* Azul distintivo */
}

.alert.alert-info {
  margin-top: 10px;
  border-left: 5px solid #0dcaf0; /* Línea distintiva */
}

.fas {
  margin-right: 5px;
}
</style>